import * as check from "../check/check";
/**
 * Функция предназначена для для возврата нового объекта, cоединяющего свойства первого объекта со свойствами второго.
 * Если свойство существует в обоих объектах, используется значение из первого объекта.
 * @param obj1 - первый объект из которого необходимо взять свойства.
 * @param obj2 - второй объект из которого необходимо взять свойства.
 * @returns новый объект, cоединяя свойства первого объекта со свойствами второго.
 */
export const mergeObjectsByLeft = (obj1, obj2) => {
    const res = {};
    for (const prop in obj1) {
        res[prop] = obj2[prop] ?? obj1[prop];
    }
    return res;
};
/**
 * Функция предназначена для получения случайного числа заданного порядка (По умолчанию 10).
 * @param len - порядок числа.
 * @returns случайное число заданного порядка.
 */
export const getRandomNumber = (len) => {
    const initialNumber = +`1E${len || 10}`;
    const min = (initialNumber / 10);
    return Math.floor(min + Math.random() * (initialNumber - min));
};
/**
 * Функция предназначена для получения случайной строки заданной длины.
 * @param len - длина строки.
 * @returns случайную строку заданной длины.
 */
export const getRandomString = (len) => {
    if (!len)
        len = 10;
    const chrs = "abdehkmnpswxzABDEFGHKMNPQRSTWXZ123456789";
    const str = new Array(len);
    for (let i = 0; i < len; i++) {
        const pos = Math.floor(Math.random() * chrs.length);
        str[i] = chrs.substring(pos, pos + 1);
    }
    if (!isNaN(+str[0]))
        str[0] = "x";
    return str.join("");
};
/**
 * Преобразует строку в объект. Строка должна быть в формате, в котором пары "ключ-значение" разделяются определенным
 * символом (по умолчанию "&"), а внутри каждой пары ключ и значение разделяются знаком равенства ("=").
 * @param string - строка, которую необходимо парсить.
 * @param separator - разделитель, который является символом, используемым для разделения различных пар ключ-значение в строке.
 * Если разделитель не указан, функция по умолчанию использует «&».
 * @returns объект, который был преобразован из строки или null, если была передана не строка.
 */
export const stringToObject = (string, separator = "&") => {
    if (!check.type.string(string))
        return null;
    const res = {};
    string.split(separator).forEach(pair => {
        if (!check.data.isEmptyString(pair)) {
            const [key, value] = pair.split("=");
            res[key] = value;
        }
    });
    return res;
};
/**
 * Преобразует объект в строку, где каждая пара ключ-значение представлена как ключ=значение, а пары разделяются
 * указанным разделителем (по умолчанию "&").
 * @param obj - объект, который необходимо преобразовать в строку.
 * @param separator - разделитель, который является символом, используемым для разделения различных пар ключ-значение
 * в результирующей строке. Если разделитель не указан, функция по умолчанию использует «&».
 * @returns преобразованную из объекта строку или null, если был передан не обьект.
 */
export const objectToString = (obj, separator = "&") => {
    if (!check.type.object(obj))
        return null;
    return Object.entries(obj).map(pair => pair.join("=")).join(separator);
};
/**
 * Фильтрует свойства объекта на основе функции фильтрации, предоставленной пользователем. Возвращает новый объект,
 * который включает только свойства, для которых функция фильтрации возвращает истинное значение.
 * @param obj - объект, свойства которого должны быть отфильтрованы.
 * @param fn - функция, которая принимает значение и ключ и возвращает истинное или ложное значение.
 * @returns новый объект, который включает только свойства, для которых функция фильтрации возвращает истинное значение или null,
 * если был передан не обьект.
 */
export const filterObject = (obj, fn) => {
    if (!check.type.object(obj))
        return null;
    if (check.data.isEmptyObject(obj) || typeof fn !== "function")
        return obj;
    const res = {};
    const keys = Object.keys(obj);
    for (const key of keys) {
        if (fn(obj[key], key))
            res[key] = obj[key];
    }
    return res;
};
/**
 * Принимает строку или число и добавляет к ней «px».
 * @param value - значение, к которому необходимо добавить "px".
 * @returns строка, к которой было добавлено «px».
 */
export const px = (value) => {
    if (!check.type.string(value) && !check.type.number(value))
        throw new Error(`${value} is not a string or number`);
    return value + "px";
};
/**
 * Cоздает promise, который разрешается через указанное время (в миллисекундах). Если время не указано или время не является
 * положительным числом, оно разрешается немедленно.
 * @param time - число, представляющее задержку в миллисекундах.
 * @returns промис.
 */
export const delay = (time) => {
    return new Promise(resolve => setTimeout(resolve, (check.type.number(time) && time > 0) ? time : 0));
};
/**
 * Принимает объект и массив строк (представляющих путь через объект) и возвращает значение по этому пути. Выдает ошибку,
 * если путь не существует в объекте или массив путей пуст.
 * @param obj - объект, из которого достается значение.
 * @param path - массив строк, каждая из которых представляет ключ в объекте или вложенный объект.
 * @returns возвращает значение по заданному пути.
 */
export const getObjValueByPath = (obj, path) => {
    if (!check.type.strictObject(obj))
        throw new Error(`${obj} is not an object`);
    if (!Array.isArray(path))
        throw new Error(`${path} is not an array`);
    if (path.length === 0)
        return undefined;
    let objectReference = obj;
    for (let i = 0; i < path.length - 1; i++) {
        if (!check.type.string(path[i]))
            throw new Error(`${path[i]} is not a string`);
        if (!check.type.strictObject(objectReference[path[i]]))
            throw new Error(`Object does not have this path - ${path}`);
        objectReference = objectReference[path[i]];
    }
    return objectReference[path[path.length - 1]];
};
/**
 * Устанавливает значение по указанному пути в объекте. Выдает ошибку, если путь не существует в объекте или массив путей пуст.
 * @param obj - объект, в котором устанавливается значение.
 * @param value - значение, которое необходимо установить.
 * @param path - массив строк, каждая из которых представляет ключ в объекте или вложенный объект.
 * @returns объект, с указанными значениями.
 */
export const setValueInObjByPath = (obj, value, path) => {
    if (!check.type.strictObject(obj))
        throw new Error(`${obj} is not an object`);
    if (!Array.isArray(path))
        throw new Error(`${path} is not an array`);
    if (path.length === 0)
        return obj;
    let objectReference = obj;
    for (let i = 0; i < path.length - 1; i++) {
        if (!check.type.string(path[i]))
            throw new Error(`${path[i]} is not a string`);
        if (check.type.strictObject(objectReference[path[i]])) {
            objectReference = objectReference[path[i]];
        }
        else {
            throw new Error(`Object does not have this path - ${path}`);
        }
    }
    if (Object.prototype.hasOwnProperty.call(objectReference, path[path.length - 1])) {
        objectReference[path[path.length - 1]] = value;
    }
    else {
        throw new Error(`Object does not have this path - ${path}`);
    }
    return obj;
};
/**
 * Принимает строку и возвращает новую строку, в которой удалены все пробельные символы.
 * @param str - cтрока, из которой удаляются пробельные символы.
 * @returns новую строку, в которой удалены все пробельные символы.
 */
export const removeSpacesFromString = (str) => {
    if (!check.type.string(str))
        throw new Error(`${str} is not a string`);
    return str.replace(/\s/g, "");
};
/**
 * Принимает строку и проверяет, начинается ли она с символа «#».  Если нет, он добавляет '#' к строке и возвращает ее.
 * @param id - идентификатор, перед которым может потребоваться '#'.
 * @returns строку с символом «#» в начале.
 */
export const htmlId = (id) => {
    if (!check.type.string(id))
        throw new Error("Element id must be a string");
    if (check.data.isEmptyString(id))
        throw new Error("Element id cannot be an empty string");
    return /^#.+/.test(id) ? id : `#${id}`;
};
/**
 * Принимает строку и проверяет, начинается ли она с «data-».  Если нет, он добавляет «data» к строке и возвращает ее.
 * @param dataAttribute - атрибут данных, перед которым может потребоваться добавление «data-».
 * @returns строку с добавлением «data-».
 */
export const htmlDataAttribute = (dataAttribute) => {
    if (!check.type.string(dataAttribute))
        throw new Error("Data attribute must be a string");
    if (check.data.isEmptyString(dataAttribute))
        throw new Error("Data attribute cannot be an empty string");
    return /^data-.+/.test(dataAttribute) ? dataAttribute : `data-${dataAttribute}`;
};
// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const getPromiseState = (promise) => {
    if (!(promise instanceof Promise))
        return Promise.reject(new Error("getPromiseState expects a promise."));
    return Promise.race([promise, "pending"]).then(value => value === "pending" ? "pending" : "resolved", () => "rejected");
};
/**
 * hasOwnProperties - проверяет, есть ли у переданного объекта все свойства из массива.
 *
 * @param obj - объект со свойствами.
 * @param keys - массив строк, содержащий свойства объекта.
 * @returns true, если все свойства из массива содержатся в объекте, иначе возвращает false.
 * @throws Error<"obj" is not an object>, если параметр obj не является объектом.
 * @throws Error<"keys" is not an array>, если параметр keys не является массивом.
 */
export const hasOwnProperties = (obj, keys) => {
    if (!check.type.strictObject(obj))
        throw new Error(`${obj} is not an object`);
    if (!Array.isArray(keys))
        throw new Error(`${keys} is not an array`);
    return keys.every(prop => Object.prototype.hasOwnProperty.call(obj, prop));
};
/**
 * parseTimeString - преобразует строку формата "hh:mm:ss" в количество секунд.
 *
 * @param timeString - строка, в которой перечислены "часы:минуты:секунды", при этом числа могут быть десятичными.
 * @returns время в секундах или null, если передан неверный параметр.
 */
export const parseTimeString = (timeString) => {
    if (!check.type.string(timeString) || !/^\d+(\.\d+)?:\d+(\.\d+)?:\d+(\.\d+)?$/.test(timeString.trim()))
        return null;
    const timeList = timeString.split(":");
    let resultSeconds = 0;
    resultSeconds += parseFloat(timeList[0]) * 3600;
    resultSeconds += parseFloat(timeList[1]) * 60;
    resultSeconds += parseFloat(timeList[2]);
    return isNaN(resultSeconds) ? null : resultSeconds;
};
/**
   * getRatioOf - определяет соотношение двух сторон.
   *
   * @param firstSide - первая сторона.
   * @param secondSide - вторая сторона.
   * @returns объект, содержащий соотношение сторон.
   * @throws Error<Parameters is not a number>, если параметр не является числом.
   * @throws Error<Parameters can not be zero or negative number>, если параметр равен нулю или является отрицательным значением.
   */
export const getRatioOf = (firstSide, secondSide) => {
    if (!check.type.number(firstSide) || !check.type.number(secondSide))
        throw new Error("Parameters is not a number");
    if (firstSide <= 0 || secondSide <= 0)
        throw new Error("Parameters can not be zero or negative number");
    const originalFirstSide = firstSide;
    const originalSecondSide = secondSide;
    firstSide = Math.max(originalFirstSide, originalSecondSide);
    secondSide = Math.min(originalFirstSide, originalSecondSide);
    while (secondSide !== 0) {
        const remainderOfDivision = firstSide % secondSide;
        firstSide = secondSide;
        secondSide = remainderOfDivision;
    }
    const ratioOfFirstSide = originalFirstSide / firstSide;
    const ratioOfSecondSide = originalSecondSide / firstSide;
    return Object.setPrototypeOf({
        x: ratioOfFirstSide,
        y: ratioOfSecondSide
    }, {
        toString() {
            return `${ratioOfFirstSide}:${ratioOfSecondSide}`;
        }
    });
};
/**
 * Создает и возвращает функцию-обертку, которая вызывает переданные функции.
 *
 * @param fns - Список функций для вызова при выполнении возвращаемой функции.
 *
 * @returns
 * Функция-обертка, которая вызывает переданные функции в порядке их передачи.
 * Если переданный элемент не является функцией, он будет проигнорирован.
 * Если не передано ни одной функции, возвращаемая функция ничего не делает.
 */
export const fnBind = (...fns) => {
    return () => {
        for (const fn of fns)
            if (typeof fn === "function")
                fn();
    };
};
/**
 * Парсит JSON строку в объект без выбрасывания ошибки.
 *
 * @param jsonStr - cтрока для преобразования.
 *
 * @returns результат парсинга в виде объекта, или `null`, если парсинг не удался.
 */
// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const safeJsonParse = (jsonStr) => {
    try {
        return JSON.parse(jsonStr);
    }
    catch (_) {
        return null;
    }
};
