import { DOM } from "@adr/utilities";

import IReply from "../interfaces/i-reply";

/**
 * Cтили для элемента iframe, используемые при синхронизации куки.
 */
export const IFRAME_COOKIE_STYLES = {
    position: "absolute",
    width: "0px",
    height: "0px",
    border: "0px",
    padding: "0px",
    margin: "0px",
} as const;

/**
 * Синхронизирует куки с использованием элемента iframe.
 *
 * @param cookieSync - конфигурация для синхронизации куки.
 */
export const cookieSync = (syncUrl: string): void => {

    const iframe = document.createElement("iframe");

    DOM.setStyles(iframe, IFRAME_COOKIE_STYLES);

    iframe.src = syncUrl;

    document.body.append(iframe);
};
