// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-nocheck

import { BANNER_MAP } from "../src/constants";
import ImgBanner from "../src/banners/img-banner/img-banner";

const storage = {
    [`banners/${BANNER_MAP[1]}`]: ImgBanner
};

// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
window.AdR = {

    async create(productName: keyof typeof storage, ...callParameters: ReadonlyArray<unknown>) {

        // eslint-disable-next-line @typescript-eslint/ban-ts-comment
        // @ts-ignore
        return new storage[productName](...callParameters);
    }

};
