import { URI, tools } from "@adr/utilities";

import IBanner from "../../interfaces/i-banner";
import IImgBannerReply from "../../interfaces/i-img-banner-reply";

/**
 * Конфигурация для создания баннера-картинки.
 */
export type ImgBannerConfig = Pick<IImgBannerReply, "href" | "src" | "alt" | "linkTarget" | "height" | "width">;

/**
 * Класс для отображения баннера-картинки.
 */
class ImgBanner implements IBanner {
    /**
     * HTML-элемент, в котором размещен баннер.
     */
    private readonly slot: HTMLElement;

    /**
     * Конфигурация баннера.
     */
    private readonly config: ImgBannerConfig;

    /**
     * HTML-элемент изображения баннера.
     */
    private readonly imgElement: HTMLImageElement;

    /**
     * HTML-элемент ссылки, который используется в баннере.
     */
    private readonly link: HTMLAnchorElement;

    /**
     * HTML-элемент обертки баннера.
     */
    private readonly bannerWrapper: HTMLDivElement;

    /**
     * @param slot - HTML-элемент, в который будет вставлен баннер.
     * @param config - конфигурация баннера.
     */
    constructor(slot: HTMLElement, config: ImgBannerConfig) {
        const encodeURL = {
            href: new URI(config.href).aabEncode().toString(),
            src: new URI(config.src).aabEncode().toString()
        }

        this.slot = slot;
        this.config = { ...config};
        this.imgElement = new Image();
        this.link = document.createElement("a");

        this.bannerWrapper = document.createElement("div");
    }

    /**
     * Загружает все необходимые ресурсы для отображения баннера.
     */
    public load(): Promise<void> {
        return new Promise((resolve, reject) => {
            this.imgElement.onload = () => resolve();
            this.imgElement.onerror = () => resolve();
            this.imgElement.src = this.config.src;
        });
    }

    /**
     * Отрисовывает все элементы баннера.
     */
    public async render(): Promise<void> {
        this.bannerWrapper.style.width = tools.px(this.config.width);
        this.bannerWrapper.style.height = tools.px(this.config.height);

        this.link.href = this.config.href;
        this.link.target = this.config.linkTarget;

        this.imgElement.alt = this.config.alt;
        this.imgElement.width = this.config.width;
        this.imgElement.height = this.config.height;

        this.link.append(this.imgElement);
        this.bannerWrapper.append(this.link);
        this.slot.append(this.bannerWrapper);
    }

    /**
     * Полностью удаляет баннер.
     */
    public destroy(): void {
        // удаляем обертку баннера, включая вложенные элементы такие как ссылка и элемент изображения
        this.bannerWrapper.remove();
    }

    /**
     * HTML-элемент, в котором размещен баннер.
     */
    public get element(): HTMLElement {
        return this.bannerWrapper;
    }
}

export default ImgBanner;
