export const isHTMLElement = (htmlObj) => {
    try {
        return htmlObj instanceof HTMLElement || htmlObj instanceof HTMLDocument;
    }
    catch (e) {
        return ((typeof htmlObj === "object") &&
            (htmlObj.nodeType === 1) &&
            (typeof htmlObj.style === "object") &&
            (typeof htmlObj.ownerDocument === "object"));
    }
};
export const isMobile = (UA) => {
    const ua = (UA || window.navigator.userAgent).toLowerCase(), reg = /android|webos|iphone|ipad|ipod|blackberry|bb|playbook|iemobile|windows phone|kindle|silk|opera mini/i;
    return reg.test(ua);
};
