import Banner from "./banner";
import { BannerConfig } from "./types";
import { makeMultiRequest } from "./utilities/make-multi-request";

/**
 * Функция обертка над классом Banner для adriver-core. 
 * Эта функция будет вызываться в adriver-core для создания баннера или
 * сразу нескольких баннеров. При создании сразу нескольких баннеров,
 * все запросы за данными баннеров будет объединены в один запрос.
 *
 * @param config - конфигурация баннера или массив конфигураций баннеров.
 * @returns - инстанс баннера или массив инстансов баннеров.
 *
 * @example
 * Пример использования с одним баннером:
 * const singleBannerConfig = { params };
 * const singleBanner = await adrcMediator(singleBannerConfig);
 *
 * Пример использования с несколькими баннерами:
 * const multipleBannerConfigs = [{ params }, { params }];
 * const multipleBanners = await adrcMediator(multipleBannerConfigs);
 */
async function adrcMediator(config: BannerConfig): Promise<Banner>;
async function adrcMediator(configs: BannerConfig[]): Promise<Banner[]>;
// eslint-disable-next-line @typescript-eslint/explicit-function-return-type
async function adrcMediator(config: BannerConfig | BannerConfig[]) {

    if (!Array.isArray(config))
        return new Banner(config);

    try {
        const requestDataArr = config.map(item => item.params);
        const response = await makeMultiRequest(requestDataArr);

        return config.map((bannerConfig, index) => new Banner(bannerConfig, response[index]));
    } catch (error) {
        console.error("Error in multi-request:", error);
        throw error;
    }
}

export default adrcMediator;
