import * as check from "../check/check";
/**
 * Устанавливает свойства элемента с помощью функции установки свойств.
 * @param element - элемент, для которого устанавливаются свойства.
 * @param properties - свойства для установки.
 * @param propertySetter - функция установки свойства.
 */
const setProperties = (element, properties, propertySetter) => {
    if ((!check.type.htmlElement(element) && !(element instanceof SVGElement)) || !check.type.strictObject(properties))
        return;
    for (const property in properties) {
        const value = check.type.number(properties[property]) ? String(properties[property]) : properties[property];
        if (check.type.string(value) && !check.data.isEmptyString(value)) {
            propertySetter(element, property, value);
        }
    }
};
/**
 * Устанавливает стили для элемента.
 * @param element - элемент, для которого устанавливаются стили.
 * @param styles - устанавливаемые стили.
 */
export const setStyles = (element, styles) => setProperties(element, styles, (el, prop, val) => el.style.setProperty(prop, val));
/**
 * Устанавливает атрибуты для элемента.
 * @param element - элемент, для которого устанавливаются атрибуты.
 * @param attributes - устанавливаемые атрибуты.
 */
export const setAttributes = (element, attributes) => setProperties(element, attributes, (el, prop, val) => el.setAttribute(prop, val));
/**
 * Вставляет элементы в другой элемент.
 * @param element - элемент, в который вставляются остальные элементы.
 * @param elements - элементы для вставки.
 */
export const insert = (element, ...elements) => {
    if (!check.type.htmlElement(element))
        return;
    const finalList = [];
    for (let i = 0; i < elements.length; i++) {
        const elem = elements[i];
        if (check.type.htmlElement(element) && elem?.nodeType === 1)
            finalList.push(elem);
    }
    element.append(...finalList);
};
