import { Second } from "@adr/types/global/types/specific";

import IImgBannerReply from "./interfaces/i-img-banner-reply";
import INoContentReply from "./interfaces/i-no-content-reply";

/**
 * Тип баннера.
 */
export enum BannerType {

    /**
     * Img баннер (баннер картинка).
     */
    Img = 1
}

/**
 * Тип окна в которм откроется ссылка пперхода.
 * 
 * @see https://developer.mozilla.org/ru/docs/Web/HTML/Element/a#attr-target
 * @see https://htmlbook.ru/html/a/target
 */
export type LinkTarget = "_top" | "_parent" | "_self" | "_blank";

/**
 * Тип JSON объекта реплая.
 * Объединяет в себе reply’и конкретных баннеров и no-content reply. 
 */
export type JSONReply = IImgBannerReply | INoContentReply;

/**
 * Конфиг баннера.
 */
export type BannerConfig = {

    /**
     * HTML-элемент или id элемента в который будет вставлен баннер.
     * Допускается отсутствие этого параметра при вызове баннера которому
     * не требуется место на странице, например fullscreen баннер.
     */
    slot?: HTMLElement | string;

    /**
     * Время в секундах через которое будет обновляться баннер.
     * При отсутствии параметра, обновления происходить не будут. 
     */
    reload?: Second;

    /**
     * Системные параметры.
     * Из этих параметров будет сформирован урл по которому будут запрошены данные баннера. 
     */
    params: Record<string, string | number>;
};
