import * as types from "./types";
/**
 * Проверяет, работает ли устройство, на котором запущено приложение, на операционной системе IOS.(iPhone, iPod или iPad).
 * Если в функцию не передан UA, то userAgent возьмется из ближайшего window.
 * @param UA - userAgent пользователя.
 * @returns true, если устройство, на котором запущено приложение, работает на операционной системе IOS, иначе false.
 */
export const IOS = (UA) => {
    const ua = (types.string(UA) ? UA : window.navigator.userAgent).toLowerCase();
    return /iphone|ipod|ipad/.test(ua);
};
/**
 * Проверяет, работает ли устройство, на котором запущено приложение, на операционной системе Android.
 * Если в функцию не передан UA, то userAgent возьмется из ближайшего window.
 * @param UA - userAgent пользователя.
 * @returns true, если устройство, на котором запущено приложение, работает на операционной системе Android, иначе false.
 */
export const android = (UA) => {
    const ua = (types.string(UA) ? UA : window.navigator.userAgent).toLowerCase();
    return /android/.test(ua);
};
/**
 * Проверяет, запущено ли приложение в WebView. Если в функцию не передан UA или win, то они возьмутся автоматически
 * из ближайшего window.
 * @param UA - userAgent пользователя.
 * @param win - window пользователя.
 * @returns true, если приложение запущено в WebView, иначе false.
 */
export const webView = (UA, win) => {
    const currentWin = win instanceof Window ? win : window, ua = (types.string(UA) ? UA : currentWin.navigator.userAgent).toLowerCase(), standalone = currentWin.navigator["standalone"], safari = /safari/.test(ua), ios = /iphone|ipod|ipad/.test(ua), wv = ua.includes("wv"), AWV = Boolean(ua.match(/(WebView|Android.*(wv|.0.0.0)|Linux; U; Android)/ig));
    return (ios && !standalone && !safari) || wv || AWV;
};
