/// <reference types="@adr/adriver-core/declaration"/> 

import { Fetcher, URI, check, tools } from "@adr/utilities";
import { AdMarking } from "@adr/ad-marking/module";
import AdriverViewability from "@adr/adriver-viewability/module";
import { AnyFunction } from "@adr/types/global/types/specific";

import { BannerConfig, JSONReply } from "./types";
import { BANNER_REPLY_URL, BANNER_MAP } from "./constants";
import IBanner from "./interfaces/i-banner";
import INoContentReply from "./interfaces/i-no-content-reply";
import { cookieSync } from "./utilities/cookie-sync";
import { executeJsCode } from "./utilities/execute-js-code";

/**
 * Минимальное время перезагрузки баннеров.
 */
export const MIN_RELOAD_TIME = 1.5;

/**
 * Код ошибки при загрузке баннера после удаления.
 * Используется только для того чтобы перейти в блок catch.
 */
const LOADING_AFTER_DESTROYING = -1;

/**
 * Класс загружает и показывает баннеры.
 */
class Banner {

    /**
     * Время в миллисекундах через которе баннер будет перезагружен.
     */
    private readonly reloadTime?: number;

    /**
     *  Урл для запроса конфигурации баннера.
     */
    private readonly bannerReplyUrl: string;

    /**
     * Массив колбэков, которые будут вызваны после завершения работы класса. 
     */
    private readonly finallyCbList: AnyFunction[];

    /**
     * HTML-элемент, в котором будет отображаться баннер.
     */
    private readonly slot: HTMLElement | null;

    /**
     * Ключ указывающий на то был ли удален баннер.
     */
    private destroyed: boolean;

    /**
     * Экземпляр класса который осуществляет показ баннера.
     */
    private banner: IBanner | null;

    /**
     * Экземпляр AV.
     */
    private av: AdriverViewability | null;

    /**
     * Экземпляр adMarking.
     */
    private adMarking: AdMarking | null;

    /**
     * Предзагруженный конфиг баннера.
     */
    private preloadReply: JSONReply | null;

    /**
     * Конфиг баннера.
     */
    private reply: Exclude<JSONReply, INoContentReply> | null;

    /**
     * Timeout id таймера перезагрузки баннера.
     */
    private reloadTimeoutId: ReturnType<typeof setTimeout> | null;

    /**
     * @param bannerConfig - Конфиг баннера.
     * @param reply - Предзагруженный конфиг баннера.
     * 
     * bannerConfig cодержит в себе данные по которым будет загружен
     * конфиг баннера (reply) который уже содержит всю информацию о конкретном креативе.
     */
    constructor(bannerConfig: BannerConfig, reply?: JSONReply) {
        console.log(bannerConfig)

        this.finallyCbList = [];
        this.bannerReplyUrl = new URI(BANNER_REPLY_URL)
            .setParams(bannerConfig.params)
            .httpslize()
            .toString();

        if (check.type.htmlElement(bannerConfig.slot))
            this.slot = bannerConfig.slot;

        else if (typeof bannerConfig.slot === "string")
            this.slot = document.querySelector(tools.htmlId(bannerConfig.slot));

        else
            this.slot = null;

        this.destroyed = false;
        this.preloadReply = reply || null;
        this.banner = null;
        this.reply = null;
        this.av = null;
        this.adMarking = null;
        this.reloadTimeoutId = null;

        if (check.type.number(bannerConfig.reload))
            this.reloadTime = Math.max(MIN_RELOAD_TIME, bannerConfig.reload) * 1000;

        this.load();
    }

    /**
     * Загружает и инициализирует баннер.
     */
    private load(): Promise<void> {

        // Если баннер будет удален (вызван метод destroy) во время загрузки, 
        // то после окончания загрузки, баннер все равно будет отрисован поскольку
        // загрузка происходит асинхронно. Для того чтобы это предотвратить,
        // на каждом этапе загрузки должна стоять проверка ключа destroyed !!!
        // который указывает на то что баннер был удален.

        return this.loadReply()

            .then(reply => {

                if (this.destroyed)
                    throw LOADING_AFTER_DESTROYING;

                if (Object.prototype.hasOwnProperty.call(reply, "noContent"))
                    throw new Error("no content");

                this.reply = reply as Exclude<JSONReply, INoContentReply>;
                console.log(reply)
                return window.AdR.create<IBanner>(`banners/${BANNER_MAP[this.reply.bannerType]}`, this.slot, reply);
            })

            .then(banner => {

                this.banner = banner;

                if (this.destroyed)
                    throw LOADING_AFTER_DESTROYING;

                return this.initBanner();
            })

            .then(() => {

                if (this.destroyed)
                    throw LOADING_AFTER_DESTROYING;

                if (this.reloadTime)
                    this.reloadTimeoutId =
                        setTimeout(() => {
                            this.reloadTimeoutId = null;
                            this.reload();
                        }, this.reloadTime);
            })

            .catch(err => {

                if (this.destroyed) // Удаляем баннера в случае загрузки после удаления
                    this.removeBanner();

                else {
                    console.log("bcl: ", err);
                    this.destroy();
                }

            });
    }

    /**
     * Загружает конфиг баннера.
     * @returns Промис который разрешится в загруженный конфиг баннера.
     */
    private async loadReply(): Promise<JSONReply> {

        if (this.preloadReply) {
            const tmp = this.preloadReply;
            this.preloadReply = null;
            return tmp;
        }

        const uri = new URI(this.bannerReplyUrl)
            .setRnd()
            .aabEncode()
            .toString();

        return new Fetcher({ uri: uri })
            .request()
            .then(response => response.json());
    }

    /**
     * Инициализирует баннер
     * @returns Промис который разрешится после инициализации баннера.
     */
    private async initBanner(): Promise<void> {

        await this.banner!.load();
        await this.banner!.render();

        this.initAV();
        this.initAdMarking();
        this.syncCookie();
        this.executeJsCode();
    }

    /**
     * Инициализирует AdMarking.
     */
    private initAdMarking(): void {
        this.adMarking = new AdMarking({
            ...this.reply!.adMarking,
            containerid: this.banner!.element
        });
    }

    /**
     * Инициализирует AV.
     */
    private initAV(): void {
        this.av = new AdriverViewability({
            ...this.reply!.av,
            nodeToTrack: this.banner!.element,
            permanent: false,
            sendEvents: true,
            sendZeroEvent: true
        });
    }

    /**
     * Запускает синхронизацию куки.
     */
    private syncCookie(): void {
        if (this.reply!.cookieSync) {
            const { url, syncAll } = this.reply!.cookieSync;
            const encodeURL = new URI(`${url}&${syncAll}`)
                .aabEncode()
                .toString();

            cookieSync(encodeURL);
        }
    }

    /**
     * Выполняет js код из конфига баннера.
     */
    private executeJsCode(): void {
        if (this.reply!.jsCode)
            executeJsCode(this.reply!.jsCode);
    }

    /**
     * Удаляет баннер который показывается в данный момент.
     */
    private removeBanner(): void {

        if (this.reloadTimeoutId)
            clearTimeout(this.reloadTimeoutId);

        this.banner?.destroy();
        this.av?.remove();
        this.adMarking?.remove();
    }

    // client api

    /**
     * Добавляет функцию обратного вызова для выполнения после завершения показа баннера.
     * @param cb - функция обратного вызова.
     */
    public finally(cb: AnyFunction): void {
        this.finallyCbList.push(cb);
    }

    /**
     * Перезагружает баннер.
     */
    public reload(): void {
        this.removeBanner();
        this.load();
    }

    /**
     * Полностью удаляет баннер.
     */
    public destroy(): void {
        this.destroyed = true;
        this.removeBanner();
        this.finallyCbList.forEach(cb => cb());
    }

}

export default Banner;
